.navbar {
  position: absolute;
  left: 0;
  top: 0;
  padding: 0;
  margin: 0;
  width: 100%;
  background: transparent;
  border-bottom: 2px solid transparent;
  z-index: 999;
  min-height: 80px;

  .icon-bar {
    color: #fff;
  }

  .navbar-nav {
    .nav-link {
      font-size: 12px;
      font-weight: 500;
      color: #fff;
      padding: 15px 7px;
      margin: 10px 15px;

      &.butn {
        padding: 12px;

        &:hover {
          color: #111;
        }
      }
    }

    .dropdown-menu {
      padding: 10px 0;
      border: 0;
      -webkit-box-shadow: 0px 10px 40px -4px rgba(0, 0, 0, 0.1);
      box-shadow: 0px 10px 40px -4px rgba(0, 0, 0, 0.1);
    }

    .dropdown-item {
      font-size: 13px;
      padding: 10px 20px;
      opacity: 0.9;

      &:hover {
        background: transparent;
        color: #12c2e9;
      }
    }
  }

  .dropdown-menu {
    display: block;
    opacity: 0;
    visibility: hidden;
    border-radius: 0;
    color: #fff;
    background: #1b1d21;
    min-width: 185px;
    -webkit-transform: translateY(20px);
    -ms-transform: translateY(20px);
    transform: translateY(20px);
    -webkit-transition: all 0.4s;
    -o-transition: all 0.4s;
    transition: all 0.4s;

    &.show {
      opacity: 1;
      visibility: visible;
      -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
      transform: translateY(0);
    }

    .dropdown-item {
      color: #fff;
      -webkit-transition: all 0.4s;
      -o-transition: all 0.4s;
      transition: all 0.4s;
      padding: 10px 25px;
      position: relative;

      &:after {
        content: '';
        width: 0px;
        height: 1px;
        background: #12c2e9;
        position: absolute;
        left: 10px;
        top: 50%;
        -webkit-transition: all 0.4s;
        -o-transition: all 0.4s;
        transition: all 0.4s;
      }

      &:hover {
        padding-left: 30px;

        &:after {
          width: 10px;
        }
      }
    }
  }

  .search {
    color: #fff;
    padding-left: 30px;
    border-left: 1px solid rgba(255, 255, 255, 0.1);

    .icon {
      cursor: pointer;
    }

    .search-form {
      position: fixed;
      width: 100%;
      height: 100vh;
      top: 0;
      left: 0;
      background: rgba(37, 37, 49, 0.98);
      display: none;

      form {
        width: 600px;
        position: absolute;
        left: calc(50% - 300px);
        top: 50%;
        -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
        transform: translateY(-50%);

        input {
          padding: 15px 0;
          font-size: 80px;
          color: #fff;
          text-transform: uppercase;
          text-align: left;
          background: transparent;
          border: 0;
          border-bottom: 2px solid #12c2e9;
        }
      }

      .close {
        position: absolute;
        top: 20%;
        right: 20%;
        color: #fff;
        font-size: 60px;
        opacity: 1;
        cursor: pointer;
      }
    }
  }

  .logo {
    width: 50px;
  }
}

.nav-scroll {
  background: #1b1d21;
  border-color: #333538;
  padding: 0;
  position: fixed;
  top: -80px;
  -webkit-transition: -webkit-transform 0.5s;
  transition: -webkit-transform 0.5s;
  -o-transition: transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
  -webkit-transform: translateY(80px);
  -ms-transform: translateY(80px);
  transform: translateY(80px);
}

.topnav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999999;
  padding: 30px 0;
  color: #fff;
  -webkit-transition: all 0.7s;
  -o-transition: all 0.7s;
  transition: all 0.7s;

  .container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }

  .container-fluid {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 0 50px;
  }

  .logo {
    width: 50px;
  }

  .menu-icon {
    margin-left: auto;
    cursor: pointer;

    &:hover .text .char {
      -webkit-animation: fadeInUp 0.3s cubic-bezier(0.3, 0, 0.7, 1) both;
      animation: fadeInUp 0.3s cubic-bezier(0.3, 0, 0.7, 1) both;
      -webkit-animation-delay: calc(30ms * var(--char-index));
      animation-delay: calc(30ms * var(--char-index));
    }

    .text {
      font-size: 13px;
      font-weight: 500;
      padding-left: 15px;
      display: table-cell;
      vertical-align: middle;
      position: relative;

      &:after {
        content: 'close';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        padding-left: 15px;
        opacity: 0;
        -webkit-transition: all 0.4s;
        -o-transition: all 0.4s;
        transition: all 0.4s;
        -webkit-transition-delay: 0.4s;
        -o-transition-delay: 0.4s;
        transition-delay: 0.4s;
      }

      .word {
        -webkit-transition: all 0.4s;
        -o-transition: all 0.4s;
        transition: all 0.4s;
        -webkit-transition-delay: 0.6s;
        -o-transition-delay: 0.6s;
        transition-delay: 0.6s;
      }

      &.open {
        .word {
          opacity: 0;
          -webkit-transition-delay: 0.4s;
          -o-transition-delay: 0.4s;
          transition-delay: 0.4s;
        }

        &:after {
          opacity: 1;
          -webkit-transition-delay: 0.6s;
          -o-transition-delay: 0.6s;
          transition-delay: 0.6s;
        }
      }
    }

    .icon {
      display: table-cell;

      i {
        display: block;
        width: 20px;
        height: 3px;
        background: #fff;
        margin: 4px 0;
      }
    }
  }

  &.dark {
    .menu-icon {
      color: #000;

      .icon i {
        background: #000;
      }
    }

    &.navlit .menu-icon {
      color: #fff;

      .icon i {
        background: #fff;
      }
    }
  }
}

.hamenu {
  position: fixed;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100vh;
  background: #18191d;
  padding: 120px 30px 30px;
  overflow: hidden;
  z-index: 9999;
  -webkit-transition: all 0.5s cubic-bezier(1, 0, 0.55, 1);
  -o-transition: all 0.5s cubic-bezier(1, 0, 0.55, 1);
  transition: all 0.5s cubic-bezier(1, 0, 0.55, 1);

  &.open {
    .menu-links .main-menu > li .link {
      -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
      transform: translateY(0);
      -webkit-transition-delay: 1s;
      -o-transition-delay: 1s;
      transition-delay: 1s;
    }

    .cont-info {
      &:after {
        height: 100vh;
        -webkit-transition-delay: 1s;
        -o-transition-delay: 1s;
        transition-delay: 1s;
      }

      .item {
        -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
        transform: translateY(0);
        opacity: 1;
        -webkit-transition-delay: 1s;
        -o-transition-delay: 1s;
        transition-delay: 1s;
      }
    }
  }

  .menu-links {
    color: #fff;
    margin-top: 30px;

    .o-hidden {
      display: inline-block;
    }

    .main-menu {
      position: relative;
      z-index: 2;

      &.gosub {
        > li {
          opacity: 0;
          visibility: hidden;
          -webkit-transition-delay: 0.2s !important;
          -o-transition-delay: 0.2s !important;
          transition-delay: 0.2s !important;

          .link {
            -webkit-transform: translateY(45px) !important;
            -ms-transform: translateY(45px) !important;
            transform: translateY(45px) !important;
            opacity: 0 !important;
            visibility: hidden !important;
            -webkit-transition-delay: 0.2s !important;
            -o-transition-delay: 0.2s !important;
            transition-delay: 0.2s !important;
          }
        }

        .sub-menu.sub-open {
          z-index: 3 !important;
          opacity: 1 !important;
          visibility: visible !important;
        }
      }

      > li {
        font-size: 30px;
        font-weight: 600;
        padding: 10px;
        width: -webkit-max-content;
        width: -moz-max-content;
        width: max-content;
        opacity: 1;
        visibility: visible;
        -webkit-transition: all 0.5s;
        -o-transition: all 0.5s;
        transition: all 0.5s;

        span.nm {
          opacity: 0.8;
          font-size: 11px;
          margin-right: 10px;
        }

        .link {
          -webkit-transform: translateY(45px);
          -ms-transform: translateY(45px);
          transform: translateY(45px);
          -webkit-transition: all 0.7s;
          -o-transition: all 0.7s;
          transition: all 0.7s;
          cursor: pointer;

          i {
            font-size: 15px;
            margin-left: 5px;
          }
        }
      }

      .sub-menu {
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        visibility: hidden;
        z-index: -1;
        -webkit-transition-delay: 0.2s;
        -o-transition-delay: 0.2s;
        transition-delay: 0.2s;

        &.sub-open {
          z-index: 3;

          li .sub-link {
            -webkit-transform: translateY(0px);
            -ms-transform: translateY(0px);
            transform: translateY(0px);
            -webkit-transition-delay: 1s;
            -o-transition-delay: 1s;
            transition-delay: 1s;
          }
        }

        li {
          color: #eee;
          font-size: 20px;
          font-weight: 400;
          letter-spacing: 1px;
          padding: 8px 10px;
          width: -webkit-max-content;
          width: -moz-max-content;
          width: max-content;

          &:hover {
            color: #fff;
          }

          .sub-link {
            -webkit-transform: translateY(45px);
            -ms-transform: translateY(45px);
            transform: translateY(45px);
            -webkit-transition: all 0.7s;
            -o-transition: all 0.7s;
            transition: all 0.7s;

            &.back {
              color: #fff;
              font-size: 20px;
              font-weight: 600;
              cursor: pointer;

              i {
                margin-left: 10px;
              }
            }

            em {
              font-size: 13px;
              font-weight: 300;
              margin-left: 10px;
            }
          }
        }
      }
    }
  }

  .cont-info {
    color: #fff;
    margin-top: 50px;
    position: relative;

    &:after {
      content: '';
      width: 1px;
      height: 0;
      background: rgba(255, 255, 255, 0.1);
      position: absolute;
      left: -40px;
      top: -170px;
      -webkit-transition: all 0.7s;
      -o-transition: all 0.7s;
      transition: all 0.7s;
    }

    .item {
      margin-bottom: 30px;
      -webkit-transform: translateY(-30px);
      -ms-transform: translateY(-30px);
      transform: translateY(-30px);
      opacity: 0;
      -webkit-transition: all 0.7s;
      -o-transition: all 0.7s;
      transition: all 0.7s;

      &:last-of-type {
        margin-bottom: 0;
      }

      h6 {
        font-size: 14px;
        font-weight: 600;
        text-transform: uppercase;
        margin-bottom: 10px;
      }
    }
  }
}
