#tsparticles {
  position: fixed;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  left: 0;
  top: 0;
  z-index: -1;
}

#particles-js {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.circle-bg {
  position: relative;

  .circle-color {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 5;

    &.fixed {
      position: fixed;
      pointer-events: none;
      opacity: 0.5;
    }
  }

  .gradient-circle {
    position: absolute;
    left: -250px;
    top: 250px;
    right: auto;
    bottom: auto;
    z-index: -1;
    width: 500px;
    height: 500px;
    border-radius: 70%;
    background-image: -webkit-linear-gradient(45deg, #059dff, #36c6f0);
    background-image: -o-linear-gradient(45deg, #059dff, #36c6f0);
    background-image: linear-gradient(45deg, #059dff, #36c6f0);
    opacity: 0.25;
    -webkit-filter: blur(100px);
    filter: blur(100px);

    &.two {
      left: auto;
      top: -250px;
      right: -250px;
      bottom: auto;
      background-image: -webkit-linear-gradient(45deg, #fb5343, #6549d5);
      background-image: -o-linear-gradient(45deg, #fb5343, #6549d5);
      background-image: linear-gradient(45deg, #fb5343, #6549d5);
    }
  }
}
